<template>
  <v-card class="mt-4" dark>
    <v-card-title>
      Intenções de Doar
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Pesquisar"
        single-line
        hide-details
      ></v-text-field>
      <v-btn fab small color="primary ml-4" dark v-on:click="add()" title="Intenção de Doar Cesta Básica">
        <i class="material-icons">add</i>
      </v-btn>
    </v-card-title>
    <v-data-table :headers="headers" :items="intencoes" :items-per-page="5" dark>
      <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="update(item.cdEntrega)">edit</v-icon>
      <v-icon small @click="remove(item.cdIntencao)">delete</v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">Reset</v-btn>
    </template>
    </v-data-table>
  </v-card>
</template>

<script>
import DataService from '../service/IntencaoDataService';
export default {
  data() {
    return {
      search: '',
      intencoes: [],
      message: "",
      headers: [
        { text: '#', align: 'start', sortable: false, value: 'cdIntencao'},
        { text: 'Doador', value: 'doador.nome' },
        { text: 'Data', align: 'center', value: 'dtIntencao' },
        { text: 'Qtd de Cestas', align: 'center', value: 'qtdCestas' },
        { text: 'Tipo de Cesta', align: 'center', value: 'tpCesta.nome' },
        { text: 'Status', align: 'center', value: 'status' },
        { text: 'Ações', align: 'center', value: 'actions', sortable: false },
      ],
    }
  },
  methods: {
    initialize(){},
    refresh() {
        DataService.retrieveAll().then((response) => {
            this.intencoes = response.data;
        });
    },
    add() {
        this.$router.push(`/intencoes/add`);
    },
    update(id) {
        this.$router.push(`/intencoes/${id}`);
    },
    remove(id) {
        DataService.remove(id).then(() => {
            this.refresh();
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
    },
  },
  created() {
      this.refresh();
  }
}
</script>

